const query_collection = `
      query Query($id: String!, $take: Int, $skip: Int, $sort: UserCollectionSortInput, $filters: ObjktFilter) {
        user(id: $id) {
        id
    objkts(take: $take, skip: $skip, sort: $sort, filters: $filters) {
        id
      assigned
      rarity
      iteration
      owner {
        id
        name
      flag
      avatarUri
      __typename
      }
      issuer {
        name
        flag
      author {
        id
          name
      flag
      avatarUri
      __typename
        }
      __typename
      }
      name
      metadata
      createdAt
      offer {
        id
        price
      __typename
      }
      __typename
    }
      __typename
  }
}
      `